const getFonts = (layers, fonts = []) => {
    if (layers.length > 0) {
        layers.forEach((layer) => {
            if (layer.type === 'Group') {
                getFonts(layer.layers, fonts);
            }
            else if (layer.type === 'Text') {
                const fontFamily = layer.style.fontFamily;
                //@ts-ignore
                const availableFamilies = NSFontManager.sharedFontManager().availableFontFamilies();
                const availableFamiliesArray = Array.from(availableFamilies, (item) => {
                    return String(item);
                });
                const fontAvailable = availableFamiliesArray.includes(fontFamily);
                if (fonts && fontAvailable && !fonts.includes(fontFamily)) {
                    fonts.push(fontFamily);
                }
            }
        });
    }
    return fonts;
};
export default getFonts;
